<template>
  <div id="map-container"></div>
</template>

<script>
export default {
  name: "aMap",
  components: {},
  mixins: [],
  props: {},
  computed: {
    mapConfig() {
      return this.$attrs.mapConfig;
    }, // mapCompsprops
  },
  watch: {},
  filters: {},
  data() {
    return {};
  },
  methods: {
    initMap() {
      var map = new AMap.Map("map-container", {
        rotateEnable: false, //  +   Ctrl + 
        pitchEnable: true, //   + Ctrl + 
        zoom: this.mapConfig.zoom, //
        pitch: this.mapConfig.pitch, //  0 - 83 
        rotation: this.mapConfig.rotation, //
        viewMode: "3D", //3D,
        zooms: [2, 20], //
        center: [this.mapConfig.center.lng, this.mapConfig.center.lat], //
        terrain: true, // 
      });
      AMap.plugin(["AMap.ControlBar", "AMap.ToolBar"], function () {
        //
        var controlBar = new AMap.ControlBar({
          //
          position: {
            right: "20px",
            bottom: "100px",
          },
        });
        map.addControl(controlBar);
        var toolBar = new AMap.ToolBar({
          //
          position: {
            right: "20px",
            bottom: "20px",
          },
        });
        map.addControl(toolBar);
      });
    },

    /**
     * , this.drawMarker({obj: {lng: 116.404, lat: 39.119}})
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-04 10:05:59
     **/
    drawMarker(params) {
      console.log(params, "DrawMarker");

      this.setMapCenterAndZoom();
      this.$emit("showMarkerDetail", {}); // 
    },

    /**
     * 
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-06 13:11:14
     **/
    drawPolyline(params) {
      console.log(params, "DrawPolyline");
      this.$emit("showPolylineDetail", {}); // showPolylineDetail
    },

    /**
     * 
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-06 13:11:14
     **/
    drawPolygon(params) {
      console.log(params, "drawPolygon");
      this.$emit("showPolygonDetail", {}); // showPolygonDetail
    },

    /**
     * 
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-04 10:20:50
     **/
    getIcon(params) {
      console.log(params, "GetIcon");
    },

    /**
     * 
     * @param: params{lat: , lng: , zoom: }
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-04 16:22:17
     **/
    setMapCenterAndZoom(params = {}) {
      console.log(params, "SetMapCenterAndZoom");
    },

    /**
     *  /
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-04 14:07:27
     **/
    removeOverlay(params) {
      console.log(params, "RemoveOverlay");
    },

    /**
     * 
     * @param:
     * @return: sectionObj: 
     * @author: mhf
     * @time: 2024-03-05 17:12:48
     **/
    showRoadCondition(params) {
      console.log(params, "ShowRoadCondition");
      let sectionObj = { a: 1 };
      this.$emit("return-sectionObj", sectionObj); // 
    },

    /**
     * 
     * @param:
     * @return:
     * @author: mhf
     * @time: 2024-03-07 14:48:53
     **/
    setViewport(pointsArr) {
      console.log(pointsArr, "SetViewport");
    },
  },
  created() {},
  mounted() {
    this.initMap();
  },
  destroyed() {},
};
</script>

<style lang="scss" scoped>
@import "../mapStyles.css";
#map-container {
}
</style>
